.image {
    width: 100%;
    height: 500px;
}

.imageDescription {
    margin: 0.5rem auto;
}

.descriptionAction {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.311);
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-evenly;
    padding: 0.5rem 0;
    width: 100%;

}

.backButtonWrap {
    position: fixed;
    left: 0;
    z-index: 2;
    
}

.buttonActions {
    cursor: pointer;
}

@media only screen and(min-width:768px){
    .wrap {
        max-width: 40%;
        margin: auto;
    }
    
}

.cardWrap {
    overflow: hidden;
}